<template>
    <div class="about">
        <commonTitle :sTitle="title" />
        <div class='refresh'>
            <p>마지막 새로고침 : {{ now }} <span class='refresh-icon'><i class="fa fa-refresh" aria-hidden="true"></i></span></p>
        </div>
        <div class='crawl-container'>
            <div class='indicator container1' id="exchange">
                <h2>환율</h2>
                <div class="economy-list" id='exchange-list'>
                    <p v-for='ex in exchange' :key='ex.title'><span class="title">{{ ex.title }}</span><span
                            class="price">{{ ex.value }}원</span> <span class="change"> {{ ex.change }}</span> <span
                            class='direction'>{{ ex.direction }}</span></p>
                </div>
            </div>
            <div class='container2'>
                <div class='indicator' id="realty">
                    <h2>유가, 금시세</h2>
                    <div class="economy-list" id='oilGold-list'>
                        <p v-for='ex in oilGold' :key='ex.title'><span class="title">{{ ex.title }}</span><span
                                class="price">{{ ex.value }}원</span> <span class="change"> {{ ex.change }}</span> <span
                                class='direction'>{{ ex.direction }}</span></p>
                    </div>
                </div>
                <div class='indicator' id="interest">
                    <h2>주요 지수</h2>
                    <div class="economy-list" id='interest-list'>
                        <p v-for='ex in Interest' :key='ex.title'><span class="title">{{ ex.title }}</span><span
                                class="price">{{ ex.value }}%</span></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import commonTitle from '../components/common-title.vue'

export default {
    name: 'EconomyView',
    created() {
        this.$http.get('/api/exchange')
            .then( (response) => {
                this.exchange = response.data
            }) .catch(err => {
                alert(err);
                console.log(err);
            })
        this.$http.get('/api/oilGold')
        .then( (response) => {
            this.oilGold = response.data
        }) .catch(err => {
            alert(err);
            console.log(err);
        })
        this.$http.get('/api/Interest')
        .then( (response) => {
            this.Interest = response.data
        }) .catch(err => {
            alert(err);
            console.log(err);
        })
    },
    data() {
        return {
            title: '주요 경제 지표',
            exchange : [],
            oilGold : [],
            Interest : [],
            now : this.getDay()
        }
    },
    components: {
        commonTitle
    },
    methods : {
        getDay() {
            let today = new Date();
            let day = `${today.getFullYear()}/${today.getMonth()+1}/${today.getDate()}`;
            let time =  `${today.getHours()}:${today.getMinutes()}:${today.getMinutes()}`;
            return `${day} ${time}`;
        },
    }
}
</script>

